<template>
  <header>
    <h1 class="logo">Stoqii.</h1>
    <template v-if="$route.name !== 'Welcome' && $route.name !== 'Error'">
      <template v-if="fetching">
        <p class="reload">
          <img src="../../assets/icon_refresh_alt.svg"
               class="spin"
               width="12"
               height="12">
          <span>Fetching...</span></p>
      </template>
      <template v-else-if="loading">
        <p class="reload">Loading...</p>
      </template>
      <template v-else>
        <a class="reload" href="javascript:;" @click="onClick">
          <img src="../../assets/icon_refresh_alt.svg"
               width="12"
               height="12">
          <span>Reload Stocks</span>
        </a>
      </template>
    </template>
  </header>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  methods: {
    ...mapActions('stocks', ['loadStocks', 'fetchAllStocks']),
    onClick() {
      this.fetchAllStocks({ userId: this.userId });
    },
  },
  computed: {
    ...mapGetters('user', ['userId']),
    ...mapGetters('stocks', ['fetching', 'loading', 'all', 'error', 'msg']),
  },
};
</script>

<style lang="scss" scoped>
@import '../../styles/theme.scss';
@import '../../styles/mixins.scss';

header {
  width: 100%;
  box-sizing: border-box;
  background: $primary;
  padding: 2px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid tint($primary, 10);
}

.logo {
  color: #fff;
  font-size: 26px;
  font-weight: 300;
  font-style: italic;
}

.reload {
  margin-right: 5px;
  color: #fff;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
  img {
    margin-top: -2px;
    margin-right: 10px;
    vertical-align: middle;
    span {
      vertical-align: middle;
    }
  }
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.spin {
  animation: spin 2s infinite linear;
}

</style>



