<template>
  <div class="stock-list">
    <div class="order-setting">
      <span class="display-number">Count: {{ stocks.length }}</span>
      <div class="order-pseudo-select">
        <p>{{ selectedOrder.name }}</p>
        <select class="order-select" name="" id="" v-model="order">
          <option
            v-for="orderItem in orderList"
            :key="orderItem.value"
            :value="orderItem.value">{{ orderItem.name }}</option>
        </select>
      </div>
    </div>
    <ul>
      <li v-for="stock in stocks" :key="stock.id">
        <stock-list-item :stock="stock" />
      </li>
    </ul>
  </div>
</template>

<script>
import _ from 'lodash';
import { mapActions, mapGetters } from 'vuex';
import StockListItem from './StockListItem.vue';

export default {
  props: ['stocks'],
  data() {
    return {
      orderList: [
        { value: '0', name: 'ストック登録順' },
        { value: '1', name: '投稿日順' },
        { value: '2', name: 'いいね数順' },
        { value: '3', name: 'コメント数順' },
      ],
      order: '0',
    };
  },
  created() {
    this.order = this.orderType;
  },
  methods: {
    ...mapActions('stocks', ['setOrderType']),
  },
  watch: {
    order(newValue) {
      this.setOrderType({ orderType: newValue });
    },
  },
  computed: {
    ...mapGetters('stocks', ['orderType']),
    selectedOrder() {
      return _.find(this.orderList, order => order.value === this.order);
    },
  },
  components: {
    StockListItem,
  },
};
</script>

<style lang="scss" scoped>
@import '../styles/mixins.scss';
@import '../styles/theme.scss';

.order-setting {
  padding: 10px 10px 0;
  display: flex;
  justify-content: space-between;
}

.display-number {
  font-size: 12px;
  line-height: 28px;
  padding: 0 10px;
  font-style: italic;
  border-bottom: 1px solid #bbb;
}

.order-pseudo-select {
  position: relative;
  font-size: 12px;
  display: block;
  vertical-align: top;

  $h: 28px;

  &:hover {
    opacity: .7;
  }
  p {
    position: absolute;
    left: 0;
    top: 0;
    height: $h;
    line-height: $h;
    width: 100%;
    padding: 0 6px;
    box-sizing: border-box;
    background: #eee;
    &:after {
      content: " ";
      background: url(../assets/arrow_carrot-down.svg) no-repeat;
      -webkit-background-size: 100% 100%;
      background-size: 100% 100%;
      width: 14px;
      height: 14px;
      position: absolute;
      right: 2px;
      top: 50%;
      margin-top: -7px;
    }
  }
  .order-select {
    position: relative;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
    height: $h;
  }
}
.stock-list {
  ul {
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
  }
  li {
    width: 25%;
    padding: 5px;
    box-sizing: border-box;
    display: flex;
  }
  @media screen and(max-width: 1200px) {
    li {
      width: 33.3333%;
    }
  }
  @media screen and(max-width: 800px) {
    li {
      width: 50%;
    }
  }
  @media screen and(max-width: 500px) {
    li {
      width: 100%;
    }
  }
}
</style>



